<template>
  <div class="cpm-cCheckbox" @click="onClick" v-if="!hidden">
    <cIcon :type="iconType" :color="iconColor" :size="'1.067rem'"></cIcon><span class="label" v-if="label">{{label}}</span>
  </div>
</template>

<script>
import cIcon from '../cIcon'

/**
 * cCheckboxcCheckboxGrouptype
 * cCheckboxvalue(v-model)currentValue
 * cCheckboxGroupselect
 */
export default {
  name: 'cCheckbox',
  components: { cIcon },
  props: {
    // checkbox/checkboxcheckboxGroup/checkboxGroup
    type: {
      type: String,
      default: 'checkbox'
    },
    // checkboxv-model
    value: {
      type: [Boolean, Number, String],
      default: false
    },
    // checkboxcheckboxGroup
    select: {
      type: Boolean,
      default: false
    },
    // checkbox
    label: {
      type: String,
      default: ''
    },
    //   
    disabled: {
      type: Boolean,
      default: false
    },
    //   
    hidden: {
      type: Boolean,
      default: false
    },
    // checkbox icon''  ['', '']
    checkboxColor: {
      type: [String, Array],
      default: () => ['#4574ff', '#C8C7CC']
    }
  },
  data () {
    return {
      currentValue: false // checkbox
    }
  },
  created () { },
  mounted () { },
  computed: {
    // checkboxColor['', '']
    checkboxColorList () {
      if (typeof this.checkboxColor === 'string') return this.checkboxColor ? [this.checkboxColor, '#C8C7CC'] : ['#4574ff', '#C8C7CC']
      return this.checkboxColor
    },
    // 
    iconType () {
      // checkboxcheckbox-filledcheckbox-disabled
      if (this.currentValue || this.select) { // 
        if (this.disabled) return 'checkbox-disabled'
        return 'checkbox-filled'
      }
      return 'checkbox'
    },
    // 
    iconColor () {
      if (this.disabled) return this.checkboxColorList[1]
      return this.checkboxColorList[0]
    }
  },
  methods: {
    onClick () {
      // disabledhidden
      if (this.disabled || this.hidden) return

      if (this.type === 'checkbox') { // checkbox
        this.currentValue = !this.currentValue
        this.$emit('input', this.currentValue) // v-model
      }
      this.$emit('onChange', this.currentValue) // checkboxGroupcheckbox
    }
  },
  watch: {
    // checkbox--v-model value
    value: {
      handler: function (val) {
        if (this.type === 'checkbox') {
          this.currentValue = val
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-cCheckbox{
  display: flex;
  align-items: center;
  .label{
    font-size: 14px;
    margin-left: 5px;
  }
}
</style>



