        {
          title: 'dialog',
          showCode: false,
          code: `this.$dialog({
  propsData: {
    showTitle: true,
    titleText: '',
    message: '0~10'
  },
  methods: {
    onCancel () {
      this.remove()
    },
    onConfirm () {
      this.remove()
    }
  }
})`
