        {
          title: '1dialog',
          showCode: false,
          code: `this.$dialog({
  propsData: {
    showTitle: true,
    showCloseBtn: true,
    titleText: '',
    message: '0~10'
  },
  methods: {
    onClose () {
      this.remove()
    },
    onCancel () {
      this.remove()
    },
    onConfirm () {
      this.remove()
    }
  }
})`
        },
        {
