          title: '2dialog',
          showCode: false,
          code: `this.$dialog({
  propsData: {
    animateName: 'bounce-in-up',
    showTitle: true,
    titleText: '',
    message: '0~10'
  },
  methods: {
    onCancel () {
      this.remove()
    },
    onConfirm () {
      this.remove()
    }
  }
})`
