    titleText: '',
    message: '0~10'
  },
  methods: {
    onCancel () {
      this.remove()
    },
    onConfirm () {
      this.remove()
    }
  }
})`
        },
        {
          title: 'dialog',
          showCode: false,
          code: `this.$dialog({
  propsData: {
    animateName: 'zoom-in',
    cContentData: {
      title: '',
      list: [
        { name: '', price: '10,000,000' },
        { name: '', price: '20,000,000' },
        { name: '', price: '30,000,000' },
        { name: '', price: '40,000,000' },
        { name: '', price: '50,000,000' },
        { name: '', price: '60,000,000' },
        { name: '', price: '70,000,000' },
        { name: '', price: '80,000,000' },
        { name: '', price: '90,000,000' }
