    showCode (item, index) {
      if (item.showCode) {
        item.showCode = false
        return
      }
      this.list.map((item) => {
        return (item.showCode = false)
      })
      this.list[index].showCode = true
    },
    showDialog (index) {
      this[`showDialog${index + 1}`]()
    },
    // dialog
    showDialog1 () {
      this.$dialog({
        propsData: {
          message: ''
        },
        methods: {
          onCancel () {
            this.remove()
          },
          onConfirm () {
            this.remove()
          }
        }
      })
    },
    // dialog
    showDialog2 () {
      this.$dialog({
        propsData: {
          showTitle: true,
          titleText: '',
          showCloseBtn: false,
          showCancelBtn: false,
          confirmBtnText: '',
          message: '2000'
        },
        methods: {
          onConfirm () {
            this.remove()
          }
        }
      })
    },
    // dialog
    showDialog3 () {
