        methods: {
          onClose () {
            this.remove()
          },
          onCancel () {
            this.remove()
          },
          onConfirm () {
            this.remove()
          }
        }
      })
    },
    // 2dialog
    showDialog5 () {
      this.$dialog({
        propsData: {
          showTitle: true,
          showCloseBtn: true,
