      this.$dialog({
        propsData: {
          animateName: 'zoom-in',
          showTitle: true,
          titleText: '',
          message: '0~10'
        },
        methods: {
          onCancel () {
            this.remove()
          },
          onConfirm () {
            this.remove()
          }
        }
      })
    },
    // dialog2
    showDialog7 () {
      this.$dialog({
        propsData: {
          animateName: 'bounce-in-up',
          showTitle: true,
          titleText: '',
