        },
        methods: {
          onCancel () {
            this.remove()
          },
          onConfirm () {
            this.remove()
          }
        }
      })
    },
    // dialog2
    showDialog8 () {
      this.$dialog({
        propsData: {
          animateName: 'bounce-in-down',
          showTitle: true,
          titleText: '',
          message: '0~10'
        },
        methods: {
          onCancel () {
            this.remove()
          },
          onConfirm () {
            this.remove()
          }
        }
      })
    },
    // dialog2
    showDialog9 () {
      this.$dialog({
        propsData: {
          animateName: 'zoom-in',
          cContentData: {
            title: '',
            list: [
              { name: '', price: '10,000,000' },
              { name: '', price: '20,000,000' },
              { name: '', price: '30,000,000' },
              { name: '', price: '40,000,000' },
              { name: '', price: '50,000,000' },
              { name: '', price: '60,000,000' },
              { name: '', price: '70,000,000' },
              { name: '', price: '80,000,000' },
              { name: '', price: '90,000,000' }
            ],
            content: ''
          }
        },
        components: {
          // 1
          // cContent: { // 
          //   props: {
          //     cContentData: {
          //       type: Object,
          //       default: () => { }
          //     }
          //   },
          //   template: `
          //     <div style="padding:1.6rem;">
          //       <h2 style="margin-bottom:1rem">{{cContentData.title}}</h2>
          //       <ul style="font-size:0.8rem;text-align:left;height:8rem;overflow-y:scroll" class="scrollable">
          //         <li style="list-style: disc;" v-for="(item, index) in cContentData.list" :key="index">{{item.name}}{{item.price}}</li>
          //       </ul>
          //       <div style="font-size:0.8rem;white-space: nowrap;overflow-x:scroll" class="scrollable">{{cContentData.content}}</div>
          //     </div>
          //   `
          // }

