<template>
  <span class="cpm-cIcon">
    <!-- unicode render -->
    <i
      class="iconfont"
      :style="cIconStyle"
      v-if="cIconRender === 'unicode'"
      @click="onClick"
      v-html="type"
    ></i>

    <!-- font-class render -->
    <i
      :class="['iconfont', cIconType]"
      :style="cIconStyle"
      v-if="cIconRender === 'font-class'"
      @click="onClick"
    ></i>

    <!-- symbol render -->
    <svg
      class="iconsymbol"
      aria-hidden="true"
      :style="cIconStyle"
      v-if="cIconRender === 'symbol'"
      @click="onClick"
    >
      <use :xlink:href="'#' + cIconType" />
    </svg>
  </span>
</template>

<script>
import '../../assets/iconfont/iconfont.js'
export default {
  name: 'cIcon',
  props: {
    // unicodefont-classsymbolfont-class
    render: {
      type: String,
      default: 'font-class'
    },
    // 
    type: {
      type: String,
      default: ''
    },
    // 
    size: {
      type: String,
      default: ''
    },
    // 
    color: {
      type: String,
      default: ''
    }
  },
  computed: {
    // 
    cIconRender () {
      return this.render
    },
    // 
    cIconType () {
      return `icon-${this.type}`
    },
    // 
    cIconStyle () {
      let style = {}
      if (this.size) {
        style['font-size'] = this.size
        style['line-height'] = this.size
      }
      if (this.color) {
        style.color = this.color
      }
      return style
    }
  },
  methods: {
    onClick (e) {
      this.$emit('onClick', e)
    }
  }
}
</script>

<style lang="scss">
@import "../../assets/iconfont/iconfont.css";
/*
https://www.iconfont.cn/help/detail?spm=a313x.7781069.1998910419.16&helptype=code

    1. unicodehtml+css
        ie6+
    2. font-classcss
        ie8+class
    3. symboljs
         ie9+svg
*/
.cpm-cIcon{
  font-size: 0; // 
  .iconfont{
    width: 1em;
    height: 1em;
    line-height: 1em;
  }
  .iconsymbol {
    width: 1em;
    height: 1em;
    line-height: 1em;
    vertical-align: middle;
    fill: currentColor;
    overflow: hidden;
  }
}
</style>



