          value: ''
        },
        {
          key: '2',
          value: ''
        }
      ],
      activeSheet: item._choiceInfo && item._choiceInfo.key
    },
    mounted () {
      this.$on('actionSheetChoiceItem', (actionSheet) => {
        self.$set(item, '_choiceInfo', actionSheet)
      })
    }
  })
}`
        },
        {
          title: '',
          showCode: false,
          code: `showActionSheet2 (item) {
  const self = this
  this.$actionSheet({
