        {
          key: 'shenzheng',
          value: ''
        }
      ],
      actionSheetTip: '',
      activeSheet: item._choiceInfo && item._choiceInfo.key
    },
    mounted () {
      this.$on('actionSheetChoiceItem', (actionSheet) => {
        self.$set(item, '_choiceInfo', actionSheet)
      })
    }
  })
}`
        },
        {
          title: '',
          showCode: false,
          code: `showActionSheet3 (item) {
  const self = this
  this.$actionSheet({
    propsData: {
      showCancelBtn: false,
      actionSheetList: [
        {
          key: '1',
          value: ''
        },
        {
          key: '0',
          value: ''
        }
      ],
      actionSheetCancelText: '',
      activeSheet: item._choiceInfo && item._choiceInfo.key
    },
    mounted () {
      this.$on('actionSheetChoiceItem', (actionSheet) => {
        self.$set(item, '_choiceInfo', actionSheet)
      })
    }
  })
}`
        },
        {
          title: 'actionSheet',
          showCode: false,
          code: `showActionSheet4 (item) {
  const self = this
  this.$actionSheet({
    propsData: {
      showCancelBtn: false,
      actionSheetList: [
        {
          key: '1',
          value: '',
          _actionsheetItemStyle: {
            color: 'red'
          }
        },
        {
          key: '2',
          value: '',
          _actionsheetItemStyle: {
            color: 'green'
          }
        }
      ],
      actionSheetCancelText: '',
      activeSheet: item._choiceInfo && item._choiceInfo.key
    },
    mounted () {
      this.$on('actionSheetChoiceItem', (actionSheet) => {
        self.$set(item, '_choiceInfo', actionSheet)
      })
    }
  })
}`
        },
        {
          title: '2s',
          showCode: false,
          code: `showActionSheet5 (item) {
  // 
  let actionSheet = this.$actionSheet({
    propsData: {
      actionSheetList: [
        {
          key: 'beijing',
          value: ''
        },
        {
          key: 'shanghai',
          value: ''
        }
      ],
      activeSheet: 'beijing'
    }
  })
  setTimeout(() => {
    actionSheet.remove()
  }, 2000)
}`
        },
        {
          title: '',
          showCode: false,
          code: `showActionSheet6 (item) {
  const self = this
  this.$actionSheet({
    propsData: {
      actionSheetList: [
