        {
          key: 'guangzhou',
          value: '',
          _actionsheetItemStyle: {
            color: 'yellow'
          }
        },
        {
          key: 'shenzheng',
          value: '',
          _actionsheetItemStyle: {
            color: 'green'
          }
        }
      ],
      activeSheet: item._choiceInfo && item._choiceInfo.key, // 
    },
    mounted () {
      this.$on('actionSheetChoiceItem', (actionSheet) => {
        self.$set(item, '_choiceInfo', actionSheet)
      })
    }
  })
}`
        },
        {
          title: '',
          showCode: false,
          code: `showActionSheet7 (item) {
  const self = this
  this.$actionSheet({
    propsData: {
      actionSheetList: [
        {
          key: 'beijing',
          value: '',
          _actionsheetItemStyle: {
            color: 'red'
          }
        },
        {
          key: 'shanghai',
          value: '',
          _actionsheetItemStyle: {
            color: 'orange'
          }
        },
        {
          key: 'guangzhou',
          value: '',
          _actionsheetItemStyle: {
            color: 'yellow'
          }
        },
        {
          key: 'shenzheng',
          value: '',
          _actionsheetItemStyle: {
            color: 'green'
          }
        }
      ],
      activeSheet: item._choiceInfo && item._choiceInfo.key, // 
      showCancelBtn: true, // 
      cliperStyleObj: {}, // 
      actionSheetWrapStyle: {}, // 
      actionSheetItemStyle: {}, // 
      actionSheetCancelStyle: {} // 
    },
    mounted () {
      this.$on('onChoiceItem', (actionSheet) => {
        self.$set(item, '_choiceInfo', actionSheet)
      })
    },
    methods: {
      // methods
      // onChoiceItem (actionSheet) {
      //   console.log(2, actionSheet)
      //   this.remove()
      // }
    }
  })
}`
        }
      ]
    }
