    showActionSheet3 (item) {
      const self = this
      this.$actionSheet({
        propsData: {
          actionSheetList: [
            {
              key: '1',
              value: ''
            }
          ],
          actionSheetItemStyle: {
            color: '#d45048'
          }, // 
          actionSheetTip: 'cpm-ui',
          actionSheetCancelText: '',
          activeSheet: item._choiceInfo && item._choiceInfo.key
        },
        mounted () {
          this.$on('onChoiceItem', (actionSheet) => {
            self.$set(item, '_choiceInfo', actionSheet)
          })
        }
      })
    },
    // actionsheet
    showActionSheet4 (item) {
      const self = this
      this.$actionSheet({
        propsData: {
          actionSheetList: [
            {
              key: '1',
              value: '',
              _actionsheetItemStyle: {
                color: 'red'
              }
            },
            {
              key: '2',
              value: '',
              _actionsheetItemStyle: {
                color: 'green'
              }
            }
          ],
          actionSheetTip: 'cpm-ui',
          activeSheet: item._choiceInfo && item._choiceInfo.key
        },
        mounted () {
          this.$on('onChoiceItem', (actionSheet) => {
            self.$set(item, '_choiceInfo', actionSheet)
          })
        }
      })
    },
    // 2s
    showActionSheet5 (item) {
      // 
      let actionSheet = this.$actionSheet({
        propsData: {
          actionSheetList: [
            {
              key: 'beijing',
              value: ''
            },
            {
              key: 'shanghai',
              value: ''
            }
          ],
          activeSheet: 'beijing'
        }
      })
      setTimeout(() => {
        actionSheet.remove()
      }, 2000)
    },
    // 
    showActionSheet6 (item) {
      const self = this
      this.$actionSheet({
        propsData: {
          actionSheetList: [
            {
              key: 0,
              value: ''
            },
            {
              key: 1,
              value: ''
            },
            {
              key: 2,
              value: ''
            }
          ],
          actionSheetList2: [
            {
              key: 3,
              value: ' >'
            }
          ],
          activeSheet: item._choiceInfo && item._choiceInfo.key // 
        },
        mounted () {
          this.$on('onChoiceItem', (actionSheet) => {
            self.$set(item, '_choiceInfo', actionSheet)
          })
        }
      })
    },
    // 
    showActionSheet7 (item) {
      const self = this
      this.$actionSheet({
        propsData: {
          actionSheetList: [
            {
              key: 'beijing',
              value: '',
              _actionsheetItemStyle: {
                color: 'red'
              }
            },
            {
              key: 'shanghai',
              value: '',
              _actionsheetItemStyle: {
                color: 'orange'
              }
            },
            {
              key: 'guangzhou',
              value: '',
              _actionsheetItemStyle: {
                color: 'yellow'
              }
            },
            {
              key: 'shenzheng',
              value: '',
              _actionsheetItemStyle: {
                color: 'green'
              }
            }
          ],
          activeSheet: item._choiceInfo && item._choiceInfo.key, // 
          actionSheetTip: 'cpm-ui', // 
          actionSheetCancelText: '', // 
          showCancelBtn: true, // 
          cliperStyleObj: {}, // 
          actionSheetWrapStyle: {}, // 
          actionSheetItemStyle: {}, // 
          actionSheetCancelStyle: {} // 
        },
        mounted () {
          this.$on('onChoiceItem', (actionSheet) => {
            console.log(1, actionSheet)
            self.$set(item, '_choiceInfo', actionSheet)
          })
        },
        methods: {
          // methods
          onChoiceItem (actionSheet) {
            console.log(2, actionSheet)
            this.remove()
          }
        }
      })
    }
  }
}
</script>



