<template>
  <div class="cpm-cRadio" @click="onClick" v-if="!hidden">
    <cIcon :type="iconType" :color="iconColor" :size="'1.067rem'"></cIcon><span class="label" v-if="label">{{label}}</span>
  </div>
</template>

<script>
import cIcon from '../cIcon'

/**
 * cRadiocRadioGrouptype
 * cRadiovalue(v-model)currentValue
 * cRadioGroupselect
 */
export default {
  name: 'cRadio',
  components: { cIcon },
  props: {
    // radio/radioradioGroup/radioGroup
    type: {
      type: String,
      default: 'radio'
    },
    // radiov-model
    value: {
      type: [Boolean, Number, String],
      default: false
    },
    // radioradioGroup
    select: {
      type: Boolean,
      default: false
    },
    // radio
    label: {
      type: String,
      default: ''
    },
    //   
    disabled: {
      type: Boolean,
      default: false
    },
    //   
    hidden: {
      type: Boolean,
      default: false
    },
    // radio icon''  ['', '']
    radioColor: {
      type: [String, Array],
      default: () => ['#4574ff', '#C8C7CC']
    }
  },
  data () {
    return {
      currentValue: false // radio
    }
  },
  computed: {
    // radioColor['', '']
    radioColorList () {
      if (typeof this.radioColor === 'string') return this.radioColor ? [this.radioColor, '#C8C7CC'] : ['#4574ff', '#C8C7CC']
      return this.radioColor
    },
    // 
    iconType () {
      // radioradio-filledradio-disabled
      if (this.currentValue || this.select) { // 
        if (this.disabled) return 'radio-disabled'
        return 'radio-filled'
      }
      return 'radio'
    },
    // 
    iconColor () {
      if (this.disabled) return this.radioColorList[1]
      return this.radioColorList[0]
    }
  },
  methods: {
    onClick () {
      // disabledhidden
      if (this.disabled || this.hidden || this.currentValue || this.select) return

      if (this.type === 'radio') { // 
        this.currentValue = true
        this.$emit('input', this.currentValue) // v-model
      }
      this.$emit('onChange') // radioGroupradio
    }
  },
  watch: {
    // radio--v-model value
    value: {
      handler: function (val) {
        if (this.type === 'radio') {
          this.currentValue = val
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-cRadio{
  display: flex;
  align-items: center;
  .label{
    font-size: 14px;
    margin-left: 5px;
  }
}
</style>



