<template>
  <div class="page-wrap index-wrap">
    <div class="top">
      <h2>cpm-ui</h2>
      <h4>A Mobile UI Toolkit for Vue 2.0</h4>
    </div>

    <div class="block">
      <div class="block-item" v-for="(item, index) in list1" :key="index">
        <router-link :to="item.path">{{item.title}}</router-link>
      </div>
    </div>

    <div class="block">
      <div class="block-item" v-for="(item, index) in list2" :key="index">
        <router-link :to="item.path">{{item.title}}</router-link>
      </div>
    </div>

    <div class="block">
      <div class="block-item" v-for="(item, index) in list3" :key="index">
        <router-link :to="item.path">{{item.title}}</router-link>
      </div>
    </div>

    <div>
      <a class="aLink" c_wrap="fs-12" href="https://github.com/cpm828/cpm-ui"></a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      list1: [
        {
          title: 'Toast',
          path: '/toast'
        },
        {
          title: 'Loading',
          path: '/loading'
        },
        {
          title: 'Dialog',
          path: '/dialog'
        },
        {
          title: 'ActionSheet',
          path: '/actionsheet'
        }
      ],
      list2: [
        {
          title: 'Icon',
          path: '/icon'
        },
        {
          title: 'Button',
          path: '/button'
        }
      ],
      list3: [
        {
          title: 'Radio',
          path: '/radio'
        },
        {
          title: 'RadioGroup',
          path: '/radiogroup'
        },
        {
          title: 'Checkbox',
          path: '/checkbox'
        },
        {
          title: 'CheckboxGroup',
          path: '/checkboxgroup'
        },
        {
          title: 'Switch',
          path: '/switch'
        }
      ]
    }
  }
}
</script>



