        2. styleUI56<br>
        3. hiddenhiddentrue
      </p>

      <div c_wrap="pb-10">
        <h3 c_wrap="fs-18 mb-10">1. {{config1.activeValue}}</h3>
        <cRadioGroup :options="config1.options" v-model="config1.activeValue" @onChange="onChange1"></cRadioGroup>
        <span class="code-btn" @click.prevent.stop="showCode(config1)">code</span>
        <code-block :code="config1.code" v-if="config1.showCode"></code-block>
      </div>

      <div c_wrap="pb-10">
        <h3 c_wrap="fs-18 mb-10">2. {{config2.activeValue}}</h3>
        <cRadioGroup :options="config2.options" v-model="config2.activeValue"></cRadioGroup>
        <span class="code-btn" @click.prevent.stop="showCode(config2)">code</span>
        <code-block :code="config2.code" v-if="config2.showCode"></code-block>
      </div>

      <div c_wrap="pb-10">
        <h3 c_wrap="fs-18 mb-10">3. disabled{{config3.activeValue}}</h3>
        <cRadioGroup :options="config3.options" v-model="config3.activeValue"></cRadioGroup>
        <span class="code-btn" @click.prevent.stop="showCode(config3)">code</span>
        <code-block :code="config3.code" v-if="config3.showCode"></code-block>
      </div>

      <div c_wrap="pb-10">
        <h3 c_wrap="fs-18 mb-10">4. hidden{{config4.activeValue}}</h3>
        <cRadioGroup :options="config4.options" v-model="config4.activeValue"></cRadioGroup>
        <span class="code-btn" @click.prevent.stop="showCode(config4)">code</span>
        <code-block :code="config4.code" v-if="config4.showCode"></code-block>
      </div>

      <div c_wrap="pb-10" class="config5">
        <h3 c_wrap="fs-18 mb-10">5. inline-block{{config5.activeValue}}</h3>
        <cRadioGroup :options="config5.options" v-model="config5.activeValue"></cRadioGroup>
        <span class="code-btn" @click.prevent.stop="showCode(config5)">code</span>
        <code-block :code="config5.code" v-if="config5.showCode"></code-block>
      </div>

      <div c_wrap="pb-10" class="config6">
        <h3 c_wrap="fs-18 mb-10">6. {{config6.activeValue}}</h3>
        <cRadioGroup :options="config6.options" v-model="config6.activeValue"></cRadioGroup>
        <span class="code-btn" @click.prevent.stop="showCode(config6)">code</span>
        <code-block :code="config6.code" v-if="config6.showCode"></code-block>
      </div>

      <div c_wrap="flex jc-c ai-c c-fff fs-16 mt-20" class="sumbit-btn" @click="getData"></div>
    </div>

    <div>
      <a class="aLink" c_wrap="fs-12"
        href="https://github.com/cpm828/cpm828.github.io/blob/master/cpm_ui/document/cRadioGroup.md"></a>
    </div>
  </div>
</template>

<script>
import codeBlock from './components/codeBlock.vue'
export default {
  name: 'RadioGroup',
  components: { codeBlock },
  data () {
    return {
      config1: {
        options: [
          {
            label: '',
            value: 'male'
          },
          {
            label: '',
            value: 'female'
          }
        ],
        activeValue: '',
        showCode: false,
        code: `config1: {
  options: [
    {
      label: '',
      value: 'male'
    },
    {
      label: '',
      value: 'female'
    }
  ],
  activeValue: '',
}

<cRadioGroup :options="config1.options" v-model="config1.activeValue"></cRadioGroup>`
      },
      config2: {
        options: [
          {
            label: '',
            value: 'male'
          },
          {
            label: '',
            value: 'female'
          }
        ],
        activeValue: 'male',
        showCode: false,
        code: `config2: {
  options: [
    {
      label: '',
      value: 'male'
    },
    {
      label: '',
      value: 'female'
    }
  ],
  activeValue: 'male',
}

<cRadioGroup :options="config2.options" v-model="config2.activeValue"></cRadioGroup>`
      },
      config3: {
        options: [
          {
            label: '',
            value: 1,
            disabled: false
          },
          {
            label: '',
            value: 2,
            disabled: false
          },
          {
            label: '',
            value: 3,
            disabled: false
          },
          {
            label: '',
            value: 4,
            disabled: true
          }
        ],
        activeValue: 1,
        showCode: false,
        code: `config3: {
  options: [
    {
      label: '',
      value: 1,
      disabled: false
    },
    {
      label: '',
      value: 2,
      disabled: false
    },
    {
      label: '',
      value: 3,
      disabled: false
    },
    {
      label: '',
      value: 4,
      disabled: true
    }
  ],
  activeValue: 1,
}

<cRadioGroup :options="config3.options" v-model="config3.activeValue"></cRadioGroup>`
      },
      config4: {
        options: [
          {
            label: '',
            value: 1,
            hidden: false
          },
          {
            label: '',
            value: 2,
            hidden: false
          },
          {
            label: '',
            value: 3,
            hidden: false
          },
          {
            label: '',
            value: 4,
            hidden: true
          }
        ],
        activeValue: 1,
        showCode: false,
        code: `config4: {
  options: [
    {
      label: '',
      value: 1,
      hidden: false
    },
    {
      label: '',
      value: 2,
      hidden: false
    },
    {
      label: '',
      value: 3,
      hidden: false
    },
    {
      label: '',
      value: 4,
      hidden: true
    }
  ],
  activeValue: 1,
}

<cRadioGroup :options="config4.options" v-model="config4.activeValue"></cRadioGroup>`
      },
      config5: {
        options: [
          {
            label: 'A',
            value: 'A'
          },
          {
            label: 'B',
            value: 'B'
          },
          {
            label: 'C',
            value: 'C'
          },
          {
            label: 'D',
            value: 'D'
          }
        ],
        activeValue: 'A',
        showCode: false,
        code: `config5: {
  options: [
    {
      label: 'A',
      value: 'A'
    },
    {
      label: 'B',
      value: 'B'
    },
    {
      label: 'C',
      value: 'C'
    },
    {
      label: 'D',
      value: 'D'
    }
  ],
  activeValue: 'A',
}

<cRadioGroup :options="config5.options" v-model="config5.activeValue"></cRadioGroup>

.config5 {
  .cpm-cRadio {
    display: inline-flex;
    margin-right: 10px;
  }
}`
      },
      config6: {
        options: [
          {
            label: 'A',
            value: 'A',
            disabled: false,
            hidden: false,
            radioColor: ['#f00', '#ccc']
          },
          {
            label: 'B',
            value: 'B',
            disabled: false,
            hidden: false,
            radioColor: ['#f00', '#ccc']
          },
          {
            label: 'C',
            value: 'C',
            disabled: false,
            hidden: false,
            radioColor: ['#f00', '#ccc']
          },
          {
            label: 'D',
            value: 'D',
            disabled: true,
            hidden: false,
            radioColor: ['#f00', '#ccc']
          },
          {
            label: 'E',
            value: 'E',
            disabled: false,
            hidden: true,
            radioColor: ['#f00', '#ccc']
          }
        ],
        activeValue: 'A',
        showCode: false,
        code: `config6: {
  options: [
    {
      label: 'A',
      value: 'A',
      disabled: false,
      hidden: false,
      radioColor: ['#f00', '#ccc']
    },
    {
      label: 'B',
      value: 'B',
      disabled: false,
      hidden: false,
      radioColor: ['#f00', '#ccc']
    },
    {
      label: 'C',
      value: 'C',
      disabled: false,
      hidden: false,
      radioColor: ['#f00', '#ccc']
    },
    {
      label: 'D',
      value: 'D',
      disabled: true,
      hidden: false,
      radioColor: ['#f00', '#ccc']
    },
    {
      label: 'E',
      value: 'E',
      disabled: false,
      hidden: true,
      radioColor: ['#f00', '#ccc']
    }
  ],
  activeValue: 'A',
}

<cRadioGroup :options="config6.options" v-model="config6.activeValue"></cRadioGroup>

.config6 {
  .cpm-cRadio {
    padding: 6px 0;
    .iconfont {
      font-size: 1.4rem !important;
    }
    .label {
      flex: 1;
      text-align: right;
      font-size: 0.9rem;
    }
  }
}`
      }
    }
  },
  methods: {
    showCode (config) {
      if (config.showCode) {
        config.showCode = false
        return
      }
      ['config1', 'config2', 'config3', 'config4', 'config5', 'config6'].forEach((item) => {
        this[item].showCode = false
      })
      config.showCode = true
    },
    onChange1 (option) {
      this.$toast(`${option.value}`)
    },
    getData () {
      let formObj = {
        'sex1': this.config1.activeValue,
        'sex2': this.config2.activeValue,
        'grade1': this.config3.activeValue,
        'grade2': this.config4.activeValue,
        'answer1': this.config5.activeValue,
        'answer2': this.config6.activeValue
      }
      // demo
      const message = `<br>${JSON.stringify(formObj)}`
      this.$dialog({
        propsData: {
          animateName: 'zoom-in',
          message,
          showCancelBtn: false,
          confrimBtnText: ''
        },
        methods: {
          onConfirm () {
            this.remove()
          }
        }
      })
    }
  }
}
</script>

<style lang="scss">
.radiogroup-wrap {
  .config5 {
    .cpm-cRadio {
      display: inline-flex;
      margin-right: 10px;
    }
  }
  .config6 {
    .cpm-cRadio {
      padding: 6px 0;
      .iconfont {
        font-size: 1.4rem !important;
      }
      .label {
        flex: 1;
        text-align: right;
        font-size: 0.9rem;
      }
    }
  }
  .sumbit-btn {
    width: 100px;
    height: 30px;
    background-image: linear-gradient(-90deg, #508bef 0%, #4b5ffe 100%);
    border-radius: 14px;
  }
}
</style>



