<template>
  <div :class="[
    'cpm-cButton',
    this.size,
    this.type,
    this.plain ? 'plain' : '' ,
    this.disabled ? 'disabled' : ''
  ]" :style="btnStyle" @click="onClick">
    <slot></slot>
  </div>
</template>

<script>
import cIcon from '../cIcon'
export default {
  name: 'cButton',
  components: { cIcon },
  props: {
    // defaultprimarydanger
    type: {
      type: String,
      default: 'default'
    },
    // : smaller/small/normal/large/
    size: {
      type: String,
      default: 'normal'
    },
    // 
    btnStyle: {
      type: Object,
      default: () => { }
    },
    // 
    plain: {
      type: Boolean,
      default: false
    },
    // 
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
    }
  },
  created () {},
  mounted () {},
  computed: {
    // iconObject
    iconOpt () {
      return Object.assign({
        position: 'left', // iconleftright
        type: '', // icon
        render: 'font-class', // font-class
        color: '',
        size: ''
      }, this.icon)
    }
  },
  methods: {
    onClick () {
      this.$emit('onClick')
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-cButton{
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  // smaller: 
  &.smaller{
    white-space: nowrap;
    display: inline-flex;
    height: 22px;
    font-size: 12px;
    padding: 0 10px;
    border-radius: 11px;
  }
  // small: 
  &.small{
    white-space: nowrap;
    display: inline-flex;
    height: 30px;
    font-size: 14px;
    padding: 0 30px;
    border-radius: 30px;
  }
  // normal: 
  &.normal{
    white-space: nowrap;
    display: inline-flex;
    height: 45px;
    font-size: 16px;
    padding: 0 50px;
    border-radius: 22.5px;
  }
  // 
  &.large{
    display: flex;
    height: 45px;
    font-size: 16px;
  }
  // 
  &.default{
    background-color: #dde2ea;
    // color: #acbed0;
    color: #8A99AB;
    &.plain{
      border: 1px solid #8A99AB;
      background-color: transparent;
      color: #8A99AB;
    }
    &.disabled {
      opacity: .6;
      cursor: not-allowed;
    }
  }
  &.primary{
    background-image: linear-gradient(90deg, #508BEF 0%, #4B5FFE 100%);
    color: #fff;
    &.plain{
      background-image: none;
      border: 1px solid #4B5FFE;
      color: #4B5FFE;
    }
    &.disabled {
      opacity: .6;
      cursor: not-allowed;
    }
  }
  &.danger{
    background-image: linear-gradient(90deg, #FE7449 0%, #FB2842 100%);
    color: #fff;
    &.plain{
      background-image: none;
      border: 1px solid #FB2842;
      color: #FB2842;
    }
    &.disabled {
      opacity: .6;
      cursor: not-allowed;
    }
  }
}
</style>



