<template>
  <div class="page-wrap checkbox-wrap">
    <div class="top">Checkbox</div>

    <div c_wrap="ta-l pl-20 pr-20">
      <p c_wrap="fs-14 mb-10 ta-j"><br>
        1. formformform<br>
        2. styleUI<br>
        3. disabledhidden
      </p>

      <p c_wrap="fs-16 pb-10">cCheckbox</p>
      <div c_wrap="pb-10">
        <cCheckbox v-model="single1" @onChange="onChange"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox v-model="single2" @onChange="onChange"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="" v-model="single3" @onChange="onChange"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="" v-model="single4" @onChange="onChange"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="" checkboxColor="#f00" v-model="single5" @onChange="onChange"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="" :checkboxColor="['#f00', '#ccc']" v-model="single6" @onChange="onChange"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="disabled" disabled v-model="single7"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="disabled" disabled v-model="single8"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="hidden" hidden v-model="single9"></cCheckbox>
      </div>
      <div c_wrap="pb-10">
        <cCheckbox label="hidden" hidden v-model="single10"></cCheckbox>
      </div>

      <span class="code-btn" @click.prevent.stop="showCode">code</span>
      <code-block type="html" :code="code" v-if="showCodeFlag"></code-block>

      <div c_wrap="flex jc-c ai-c c-fff fs-16 mt-20" class="sumbit-btn" @click="getData"></div>
    </div>

    <div>
      <a class="aLink" c_wrap="fs-12"
        href="https://github.com/cpm828/cpm828.github.io/blob/master/cpm_ui/document/cCheckbox.md"></a>
    </div>
  </div>
</template>

<script>
import codeBlock from './components/codeBlock.vue'
export default {
  name: 'Checkbox',
  components: { codeBlock },
  data () {
    return {
      single1: true,
      single2: false,
      single3: true,
      single4: false,
      single5: true,
      single6: false,
      single7: true,
      single8: false,
      single9: true,
      single10: false,
      showCodeFlag: false,
      code: `<cCheckbox v-model="single1" @onChange="onChange"></cCheckbox>
<cCheckbox v-model="single2" @onChange="onChange"></cCheckbox>
<cCheckbox label="" v-model="single3" @onChange="onChange"></cCheckbox>
<cCheckbox label="" v-model="single4" @onChange="onChange"></cCheckbox>
<cCheckbox label="" checkboxColor="#f00" v-model="single5" @onChange="onChange"></cCheckbox>
<cCheckbox label="" :checkboxColor="['#f00', '#ccc']" v-model="single6" @onChange="onChange"></cCheckbox>
<cCheckbox label="disabled" disabled v-model="single7"></cCheckbox>
<cCheckbox label="disabled" disabled v-model="single8"></cCheckbox>
<cCheckbox label="hidden" hidden v-model="single9"></cCheckbox>
<cCheckbox label="hidden" hidden v-model="single10"></cCheckbox>
`
    }
  },
  methods: {
    showCode () {
      this.showCodeFlag = !this.showCodeFlag
    },
    onChange (flag) {
      this.$toast(flag ? '' : '')
    },
    getData () {
      let formObj = {
        single1: this.single1,
        single2: this.single2,
        single3: this.single3,
        single4: this.single4,
        single5: this.single5,
        single6: this.single6,
        single7: this.single7,
        single8: this.single8,
        single9: this.single9,
        single10: this.single10
      }
      // demo
      const message = `<br>${JSON.stringify(formObj)}`
      this.$dialog({
        propsData: {
          animateName: 'zoom-in',
          message,
          showCancelBtn: false,
          confrimBtnText: ''
        },
        methods: {
          onConfirm () {
            this.remove()
          }
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.checkbox-wrap{
  .sumbit-btn{
    width: 100px;
    height: 30px;
    background-image: linear-gradient(-90deg, #508BEF 0%, #4B5FFE 100%);
    border-radius: 14px;
  }
}
</style>



