<template>
  <div class="cpm-cRadioGroup">
    <template v-for="(option, index) in radioGroupOpt">
      <cRadio :key="index" type="radioGroup" :label="option.label" :select="option._select" :disabled="option.disabled"
        :hidden="option.hidden" :radioColor="option.radioColor" @onChange="onChange(option)"></cRadio>
    </template>
  </div>
</template>

<script>
import cRadio from '../cRadio'

/**
 * 
 * 1. watch value(currentValue) -> watch currentValue
 * 2. $on onChange()-> watch currentValue(value)-> watch value
 */
export default {
  name: 'cRadioGroup',
  components: { cRadio },
  props: {
    // v-model
    value: {
      type: [Number, String, Boolean],
      default: false
    },
    // listlabelvaluedisabledhiddenlabelStyle
    options: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      currentValue: '',
      radioGroupOpt: []
    }
  },
  created () {
    // _select
    this.radioGroupOpt = this.options.map((item) => {
      return Object.assign({}, item, { _select: this.value === item.value })
    })
  },
  watch: {
    currentValue (val) {
      this.$emit('input', val)
    },
    value: {
      handler: function (val) {
        this.currentValue = val
      },
      immediate: true
    }
  },
  methods: {
    // radioradio
    onChange (option) {
      this.radioGroupOpt.forEach((item) => {
        item = Object.assign(item, { '_select': false })
      })
      option._select = true
      this.currentValue = option.value
      this.$emit('onChange', option) // 
    }
  }
}
</script>

<style>
</style>



