<template>
  <div class="page-wrap toast-wrap">
    <div class="top">Toast</div>

    <div class="block">
      <div class="block-item" :class="{'active': item.showCode}" v-for="(item, index) in list" :key="index">
        <a @click.prevent="showToast(index)">
          <span class="text">{{index + 1}}{{item.title}}</span>
          <i @click.prevent.stop="showCode(item, index)">code</i>
        </a>
        <code-block :code="item.code" v-if="item.showCode"></code-block>
      </div>
    </div>

    <div>
      <a class="aLink" c_wrap="fs-12"
        href="https://github.com/cpm828/cpm828.github.io/blob/master/cpm_ui/document/Toast.md"></a>
    </div>
  </div>
</template>

<script>
import codeBlock from './components/codeBlock.vue'
export default {
  name: 'Toast',
  components: { codeBlock },
  data () {
    return {
      list: [
        {
          title: '',
          showCode: false,
          code: `this.$toast('toast')`
        },
        {
          title: '',
          showCode: false,
          code: `this.$toast({
  propsData: {
    message: '3s',
    time: 3000
  }
})`
        },
        {
          title: '',
          showCode: false,
          code: `this.$toast({
  propsData: {
    message: '',
    position: 'top'
  }
})`
        },
        {
          title: 'toast',
          showCode: false,
          code: `this.$toast({
  propsData: {
    message: 'toast',
    wrapStyleObj: {
      'background': '#666'
    }
  }
})`
        },
        {
          title: 'icontoast',
          showCode: false,
          code: `this.$toast({
  propsData: {
    message: 'icontoast',
    type: 'success'
  }
})
 `
        },
        {
          title: 'toast',
          showCode: false,
          code: `this.$toast({
  propsData: {
    message: 'toast',
    type: 'success'
  },
  methods: {
    callback () {
      alert('toast ')
    }
  }
})
 `
        },
        {
          title: '',
          showCode: false,
          code: `this.$toast({
  propsData: {
    message: '',
    type: 'info',
    time: 3000,
    position: 'middle',
    wrapStyleObj: {
      'background': '#4c4c4c'
    }
  },
  methods: {
    callback () {
        alert('toast ')
    }
  }
})
 `
        }
      ]
    }
  },
  methods: {
    showCode (item, index) {
      if (item.showCode) {
        item.showCode = false
        return
      }
      this.list.map((item) => {
        return (item.showCode = false)
      })
      this.list[index].showCode = true
    },
    showToast (index) {
      this[`showToast${index + 1}`]()
    },
    // toast
    showToast1 () {
      this.$toast('toast')
    },
    // 
    showToast2 () {
      this.$toast({
        propsData: {
          message: '3s',
          time: 3000
        }
      })
    },
    // 
    showToast3 () {
      this.$toast({
        propsData: {
          message: '',
          position: 'top'
        }
      })
    },
    // toast
    showToast4 () {
      this.$toast({
        propsData: {
          message: 'toast',
          wrapStyleObj: {
            'background': '#666'
          }
        }
      })
    },
    // icontoast
    showToast5 () {
      this.$toast({
        propsData: {
          message: 'icontoast',
          type: 'success',
          time: 3000
        }
      })
    },
    // toast
    showToast6 () {
      this.$toast({
        propsData: {
          message: 'toast',
          type: 'success'
        },
        methods: {
          callback () {
            alert('toast ')
          }
        }
      })
    },
    // 
    showToast7 () {
      // propspropsDatapropsDatadatacomputedmethodswatch
      this.$toast({
        propsData: {
          message: '',
          type: 'info',
          time: 3000,
          position: 'middle',
          wrapStyleObj: {
            'background': '#4c4c4c'
          }
        },
        methods: {
          callback () {
            alert('toast ')
          }
        }
      })
    }
  }
}
</script>



