<template>
  <div class="cpm-dialog" @touchstart="touchstartDialog($event)" @touchmove="touchmoveDialog($event)">
    <transition name="dialog-fade">
      <div class="cpm-dialog-cliper" :style="cliperStyleObj" @click="onClickCliper" v-show="animateShow"></div>
    </transition>

    <transition :name="animateName" v-on:after-leave="afterLeave">
      <div class="cpm-dialog-wrap" ref="cpmDialog" :style="dialogWrapStyle" v-show="animateShow">
        <!--  -->
        <div class="cpm-main" :style="contentWrapStyle" v-if="message">
          <div class="cpm-title" v-html="titleText" v-if="showTitle"></div>
          <div class="cpm-content" v-html="message" :style="messageStyle"></div>
        </div>
        <!--  -->
        <cContent :cContentData="cContentData"></cContent>

        <div class="cpm-btn-box">
          <div class="cancel" v-if="showCancelBtn" :style="cancelBtnStyle" @click="onCancel">{{cancelBtnText}}</div>
          <div class="sure" v-if="showConfirmBtn" :style="confirmBtnStyle" @click="onConfirm">{{confirmBtnText}}</div>
        </div>

        <template v-if="showCloseBtn">
          <!--  -->
          <cIcon type="close-simple" color="#333" size="1.2rem" class="close-simple"
            v-if="closeBtnPostion === 'rightTop'" @cIconClick="onClose"></cIcon>
          <!--  -->
          <cIcon type="close" color="white" size="1.7rem" class="close" v-else @cIconClick="onClose"></cIcon>
        </template>
      </div>
    </transition>
  </div>
</template>

<script>
import cIcon from '../cIcon'
export default {
  name: 'cDialog',
  components: {
    cIcon,
    cContent: {
      template: '<div></div>'
    }
  },
  props: {
    // 
    cliperStyleObj: {
      type: Object,
      default: () => { }
    },
    // 
    dialogWrapStyle: {
      type: Object,
      default: () => { }
    },
    // 
    contentWrapStyle: {
      type: Object,
      default: () => { }
    },
    // ''/zoom-in/bounce-in-up/bounce-in-down
    animateName: {
      type: String,
      default: ''
    },
    // 
    showTitle: {
      type: Boolean,
      default: false
    },
    // html
    titleText: {
      type: String,
      default: ''
    },
    // html
    message: {
      type: String,
      default: ''
    },
    // left/center/right
    messageAlign: {
      type: String,
      default: 'center'
    },
    // 
    showCloseBtn: {
      type: Boolean,
      default: false
    },
    // rightTop/bottom
    closeBtnPostion: {
      type: String,
      default: 'rightTop'
    },
    // 
    showCancelBtn: {
      type: Boolean,
      default: true
    },
    // 
    cancelBtnText: {
      type: String,
      default: ''
    },
    // 
    cancelBtnStyle: {
      type: Object,
      default: () => { }
    },
    // 
    showConfirmBtn: {
      type: Boolean,
      default: true
    },
    // 
    confirmBtnText: {
      type: String,
      default: ''
    },
    // 
    confirmBtnStyle: {
      type: Object,
      default: () => { }
    },
    // 
    cContentData: {
      type: Object,
      default: () => { }
    }
  },
  data () {
    return {
      animateShow: false, // 
      scrollableList: null, // 
      currentScrollArea: null, // 
      dialogMouseInfo: {
        startX: 0,
        startY: 0,
        endX: 0,
        endY: 0
      } // 
    }
  },
  mounted () {
    this.animateShow = true
    this.$nextTick(() => {
      if (this.scrollableList === null) {
        this.scrollableList = this.$refs.cpmDialog.querySelectorAll('.scrollable') // scrollable
      }
    })
  },
  computed: {
    messageStyle () {
      return {
        'text-align': this.messageAlign
      }
    }
  },
  methods: {
    touchstartDialog (e) {
      this.currentScrollArea = null
      this.dialogMouseInfo.startX = e.targetTouches[0].clientX
      this.dialogMouseInfo.startY = e.targetTouches[0].clientY
      // touchmove
      this.scrollableList.forEach((el) => {
        if (el.contains(e.target)) {
          this.currentScrollArea = el
        }
      })
    },
    touchmoveDialog (e) {
      this.dialogMouseInfo.endX = e.targetTouches[0].clientX
      this.dialogMouseInfo.endY = e.targetTouches[0].clientY
      const { startX, startY, endX, endY } = this.dialogMouseInfo

      // 
      let directionY = ''
      if (endY > startY) {
        directionY = 'down'
      } else if (endY < startY) {
        directionY = 'up'
      }

      let directionX = ''
      if (endX > startX) {
        directionX = 'right'
      } else if (endX < startX) {
        directionX = 'left'
      }

      /**
       * 1. 
       * 2. 
       *    2.1 el.scrollLeft=0scorllWidth-offsetWidth 
       *    2.2 el.scrollTop=0scorllHeight-offsetHeight 
       */
      const el = this.currentScrollArea
      if (!el || (el &&
        (
          (directionX === 'right' && el.scrollLeft === 0) ||
          (directionX === 'left' && el.scrollLeft >= (el.scrollWidth - el.offsetWidth))
        ) &&
        (
          (directionY === 'down' && el.scrollTop === 0) ||
          (directionY === 'up' && el.scrollTop >= (el.scrollHeight - el.offsetHeight))
        )
      )) {
        e.preventDefault()
      }
    },
    // 
    remove () {
      this.animateShow = false
    },
    // transition leave
    afterLeave () {
      this._remove()
    },
    // domhash
    _remove () {
      if (this.$el && this.$el.parentNode) {
        this.$el.parentNode.removeChild(this.$el)
        this.$destroy()
        window.removeEventListener('hashchange', this.remove)
      }
    },
    // 
    onClickCliper () {
      this.$emit('onClickCliper')
    },
    // 
    onClose () {
      this.$emit('onClose')
    },
    onCancel () {
      this.$emit('onCancel')
    },
    onConfirm () {
      this.$emit('onConfirm')
    }
  }
}
</script>

<style lang="scss" scoped>
.cpm-dialog {
  .cpm-dialog-cliper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(14, 14, 14, 0.8);
    z-index: 888;
  }
  .cpm-dialog-wrap {
    width: 270px;
    border-radius: 8px;
    background-color: #fff;
    z-index: 999;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(0, -50%);
    margin-left: -135px;
    .cpm-main {
      font-size: 14px;
      color: #333;
      padding: 30px;
      .cpm-title {
        font-size: 17px;
        color: #333;
        font-weight: bold;
        margin-bottom: 16px;
      }
      .cpm-content{
        word-break: break-all;
      }
    }
    .cpm-btn-box {
      height: 45px;
      display: flex;
      border-top: 1px solid #d8d8d8;
      div {
        display: flex;
        justify-content: center;
        align-items: center;
        flex: 1;
        font-size: 15px;
        color: #333;
        &.cancel {
          border-right: 1px solid #d8d8d8;
        }
        &.sure {
          // color: #415af6;
        }
      }
    }
    .close-simple {
      position: absolute;
      right: 10px;
      top: 10px;
    }
    .close {
      position: absolute;
      bottom: -62px;
      left: 50%;
      margin-left: -16px;
    }
  }

  // 
  .dialog-fade-enter,
  .dialog-fade-leave-to {
    opacity: 0;
  }
  .dialog-fade-enter-active {
    transition: opacity 0.3s;
  }
  .dialog-fade-leave-active {
    transition: opacity 0.3s 0.2s;
  }
  .dialog-fade-enter-to,
  .dialog-fade-leave {
    opacity: 1;
  }
  // 1zoom-in
  .zoom-in-enter-active {
    animation: zoom-in 0.5s;
  }
  .zoom-in-leave-active {
    animation: zoom-in 0.5s reverse;
  }
  @keyframes zoom-in {
    0% {
      transform: translate(0, -50%) scale(0); // 
    }
    70% {
      transform: translate(0, -50%) scale(1);
    }
    85% {
      transform: translate(0, -50%) scale(0.95);
    }
    100% {
      transform: translate(0, -50%) scale(1);
    }
  }
  // 2bounce-in-up
  .bounce-in-up-enter-active,
  .bounce-in-up-leave-active {
    transition: transform 0.5s;
  }
  .bounce-in-up-enter,
  .bounce-in-up-leave-active {
    transform: translateY(700px);
  }
  // 3bounce-in-down
  .bounce-in-down-enter-active,
  .bounce-in-down-leave-active {
    transition: transform 0.5s;
  }
  .bounce-in-down-enter,
  .bounce-in-down-leave-active {
    transform: translateY(-700px);
  }
}
</style>



