<template>
  <div class="page-wrap icon-wrap">
    <div class="top">Icon</div>

    <div class="block">
      <div
        class="block-item"
        :class="{'active': item.showCode}"
        v-for="(item, index) in list"
        :key="index"
      >
        <a>
          <span class="text">{{index + 1}}{{item.title}}</span>
          <i @click.prevent.stop="showCode(item, index)">code</i>
        </a>
        <code-block type="html" :code="item.code" v-if="item.showCode"></code-block>
      </div>
    </div>

    <div class="block">
      <div c_wrap="ta-l p-20">
        <p c_wrap="fs-18 c_000">icon</p>
        <p c_wrap="fs-14 ta-j">1. iconunicodefont-classsymbolfont-classsymboltype</p>
        <p c_wrap="fs-14 ta-j">2. font-classsymbolicon-icon-close</p>
        <p c_wrap="fs-18 ta-l mt-20">icon~</p>
      </div>
      <div c_wrap="flex ai-c fw-w">
        <div
          style="width:25%;height:3.2rem;margin-bottom:10px"
          c_wrap="flex fd-c jc-fs"
          v-for="(item, index) in list2"
          :key="index"
        >
          <cIcon render="unicode" :type="item.unicode" :color="iconColor" size="1.3rem" @onClick="cIconClick" :key="iconColor"></cIcon>
          <i c_wrap="fs-10 c-333">{{item.unicode}}</i>
          <i c_wrap="fs-10 c-333">{{item.font}}</i>
          <i c_wrap="fs-10 c-333">{{item.symbol}}</i>
        </div>
      </div>
      <div>
        <a class="aLink" c_wrap="fs-12"
          href="https://cpm828.github.io/cpm_ui/fontdemo/demo_index.html">icon</a>
      </div>
      <div>
        <a class="aLink" c_wrap="fs-12"
          href="https://github.com/cpm828/cpm828.github.io/blob/master/cpm_ui/document/cIcon.md"></a>
      </div>
    </div>
  </div>
</template>

<script>
import codeBlock from './components/codeBlock.vue'
export default {
  name: 'Icon',
  components: { codeBlock },
  data () {
    return {
      list: [
        {
          title: 'unicodeie6+',
          showCode: false,
          code: `<cIcon render="unicode" type="&#xe699;" color="white" size="15px" @onClick="cIconClick"></cIcon>`
        },
        {
          title: 'font-classie8+',
          showCode: false,
          code: `<cIcon render="font-class" type="delete" color="white" size="2rem" @onClick="cIconClick"></cIcon>`
        },
        {
          title: 'symbolie9+svg',
          showCode: false,
          code: `<cIcon render="symbol" type="delete" color="white" size="2rem" @onClick="cIconClick"></cIcon>`
        }
      ],
      list2: [
        // 1
        {
          unicode: '&#xe65e;',
          font: 'check'
        },
        {
          unicode: '&#xe660;',
          font: 'check-filled'
        },
        {
          unicode: '&#xe65f;',
          font: 'close'
        },
        {
          unicode: '&#xe65d;',
          font: 'close-filled'
        },
        // 2
        {
          unicode: '&#xe661;',
          font: 'forbidden'
        },
        {
          unicode: '&#xe662;',
          font: 'forbidden-filled'
        },
        {
          unicode: '&#xe666;',
          font: 'help'
        },
        {
          unicode: '&#xe665;',
          font: 'help-filled'
        },
        // 3
        {
          unicode: '&#xe664;',
          font: 'info'
        },
        {
          unicode: '&#xe663;',
          font: 'info-filled'
        },
        {
          unicode: '&#xe68b;',
          font: 'info-down'
        },
        {
          unicode: '&#xe68c;',
          font: 'info-down-filled'
        },
        // 4
        {
          unicode: '&#xe751;',
          font: 'home'
        },
        {
          unicode: '&#xe750;',
          font: 'home-filled'
        },
        {
          unicode: '&#xe65c;',
          font: 'refresh'
        },
        {
          unicode: '&#xe63f;',
          font: 'search'
        },
        // 5
        {
          unicode: '&#xe72f;',
          font: 'radio'
        },
        {
          unicode: '&#xe71e;',
          font: 'radio-filled'
        },
        {
          unicode: '&#xe72f;',
          font: 'radio(disabled)'
        },
        {
          unicode: '&#xe71f;',
          font: 'radio-disabled'
        },
        // 6
        {
          unicode: '&#xe720;',
          font: 'checkbox'
        },
        {
          unicode: '&#xe721;',
          font: 'checkbox-filled'
        },
        {
          unicode: '&#xe720;',
          font: 'checkbox(disabled)'
        },
        {
          unicode: '&#xe722;',
          font: 'checkbox-disabled'
        },
        // 7
        {
          unicode: '&#xe66b;',
          font: 'star'
        },
        {
          unicode: '&#xe66c;',
          font: 'star-filled'
        },
        {
          unicode: '&#xe6e1;',
          font: 'volume-off'
        },
        {
          unicode: '&#xe6e3;',
          font: 'volume'
        },
        // 8
        {
          unicode: '&#xe66d;',
          font: 'arrow-up'
        },
        {
          unicode: '&#xe66f;',
          font: 'arrow-down'
        },
        {
          unicode: '&#xe66e;',
          font: 'arrow-left'
        },
        {
          unicode: '&#xe670;',
          font: 'arrow-right'
        },
        // 9
        {
          unicode: '&#xe677;',
          font: 'show-less'
        },
        {
          unicode: '&#xe678;',
          font: 'show-more'
        },
        {
          unicode: '&#xe675;',
          font: 'page-turn-left'
        },
        {
          unicode: '&#xe676;',
          font: 'page-turn-right'
        },
        // 10
        {
          unicode: '&#xe687;',
          font: 'benefits'
        },
        {
          unicode: '&#xe69e;',
          font: 'close-simple'
        },
        {
          unicode: '&#xe699;',
          font: 'delete'
        },
        {
          unicode: '&#xe640;',
          font: 'edit'
        }
      ],
      iconColorArr: ['#333', '#4080e8', '#079a07', '#27c3d8', '#960596'],
      iconColor: '#333'
    }
  },
  methods: {
    showCode (item, index) {
      if (item.showCode) {
        item.showCode = false
        return
      }
      this.list.map((item) => {
        return (item.showCode = false)
      })
      this.list[index].showCode = true
    },
    cIconClick () {
      this.iconColor = this.iconColorArr[Math.floor(Math.random() * this.iconColorArr.length)]
    }
  }
}
</script>

<style lang="scss">
</style>



